import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-community-parking-availablity',
  templateUrl: './community-parking-availablity.component.html',
  styleUrls: ['./community-parking-availablity.component.scss']
})
export class CommunityParkingAvailablityComponent implements OnInit {


  unitAllocationHeader:any
  unitAllocationData:any;
  gridSourceData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor() { }

  ngOnInit() {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.unitAllocationHeader = [{
         text: 'SLOT NO',
         datafield: 'slotType',
         width: 100,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'SLOT BLOCK',
         datafield: 'slotBlockNo',
         minwidth: 100,
         cellsrenderer:cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'SLOT UNIT',
         datafield: 'tempBlock',
         cellsrenderer:cellsrenderer,
         minwidth: 100,
         renderer: columnrenderer
     }, {
         text: 'FULL NAME',
         datafield: 'tempUnit',
         cellsrenderer: cellsrenderer,
         minwidth: 100,
         renderer: columnrenderer
     }, {
         text: 'START DATE',
         datafield: 'startDateTime',
         cellsrenderer:cellsrenderer,
         minwidth: 100,
         renderer: columnrenderer
     }, {
         text: 'START TIME',
         datafield: 'endDateTime',
         cellsrenderer: cellsrenderer,
         minwidth: 100,
         renderer: columnrenderer
     }, {
         text: 'END DATE',
         datafield: 'slotRateType',
         cellsrenderer: cellsrenderer,
         minwidth: 100,
         renderer: columnrenderer
     }, {
        text: 'END TIME',
        datafield: 'price',
        cellsrenderer: cellsrenderer,
        minwidth: 100,
        renderer: columnrenderer
     },{
         text: 'CREATED BY',
         cellsalign: 'status', 
         align: 'center',
         width: 100,
         cellsrenderer:cellsrenderer,
         renderer: columnrenderer
     }];

    //  var ticketListData = res.filter(data => {
    //   return data.isActive
    // })

   let localData=[
     {
      slotType:'Unit Owned',
      slotBlockNo:'1001',
      tempBlock:'Temp Block',
      tempUnit:'Temp Unit',
      startDateTime:"Start Date Time",
      endDateTime:"End Date Time",
      slotRateType:"Slot Rate Type",
      Price:'1000',
      status:'Active'
     }
   ]

    this.gridSourceData = {
      localdata: localData,
      datatype: "array"
    }

    this.unitAllocationData = new jqx.dataAdapter(this.gridSourceData);
  }

}
